export default {
    data() {
        return {
            value:'',show: false,
            yhimg:'http://www.homeamc.cn/pm/static/banklogo/YH.png',
        }
    },
    beforeCreate(){
        this.$store.dispatch('banklist')
    },
    computed: {
        user(){
            if(this.$store.state.user == '') this.$store.commit('USER')
            return this.$store.state.user
        },
        index(){
            if(this.$store.state.index == '') this.$store.commit('INDEX')
            return this.$store.state.index
        },
        card(){
            let card = ''
            for(let val of this.$store.state.banklist){
                val.isDefault == 1 ? card = val : null
            }
            return card
        },
    },
    created(){
        document.title = '租金提取'

        this.$nextTick(()=>{
            // for(let val of this.$store.state.user.bankCards){
            //     console.log(val)
            //     val.isDefault == 1 ? this.card = val : null
            // }
            // console.log(this.card)
            let yhName = {
                '北京银行':'BJYH','工商银行':'GSYH','光大银行':'GDYH','华夏银行':'HXYH','建设银行':'JSYH','交通银行':'JTYH','民生银行':'MSYH','南京银行':'NJYH','宁波银行':'NBYH',
                '农业银行':'NYYH','浦发银行':'PFYH','深圳发展银行':'SZFZYH','兴业银行':'XYYH','邮政银行':'YZYH','招商银行':'ZSYH','中国银行':'ZGYH','中信银行':'ZXYH'
            };
            let bankNames = this.$store.state.user.bankCards ? this.card.bankName : ''
            for(let key in yhName){
                if(bankNames.indexOf(key) >= 0){
                    this.yhimg = `http://www.homeamc.cn/pm/static/banklogo/${yhName[key]}.png`
                    break
                }
            }
        })
    },
    methods: {
        RentExtractionClicks(){
            this.show = true
        },
        RentExtractionClick() {
            this.show = false
            if(this.value < 100){
                this.$dialog.alert({
                    title: '温馨提示',
                    message: '单比提现金额必须大于100元！'
                }).then(() => {
                // on close
                });
                return
            }
            if(this.value > this.index.balance){
                this.$dialog.alert({
                    title: '温馨提示',
                    message: '余额不足！'
                }).then(() => {
                // on close
                });
                return
            }
            if(this.$store.state.isLoading == false){
                this.$store.dispatch('draw', parseFloat(this.value))
                // this.$dialog.confirm({
                //     title: '确认体现吗？',
                //     message: ''
                // }).then(() => {
                //     this.$store.dispatch('draw', parseFloat(this.value))
                // }).catch(() => {
                // // on cancel
                // });
            }
        },
        shop(){
            window.location.href = 'http://www.homeamc.cn/shops/wx/shops?wxh=sz_fangyuanli'
        }
    },
    filters:{
        filter(val){
            return `${val.substring(0,4)} **** **** ${val.substring(val.length - 5,val.length)}`
        }
    }
}



